import React, { Component } from "react";
import { CCard, CCardBody } from "@coreui/react-pro";
export default class ChainSchoolDashboard extends Component {
  constructor(props) {
    super(props);
    let school_id = this.props.match.params.school_id;
    this.state = {
      school_id: school_id,
    };
  }
  render() {
    let { school_id } = this.state;
    return (
      <CCard>
        <CCardBody></CCardBody>
      </CCard>
    );
  }
}
